* {
    padding: 0;
    margin: 0;
}
header {
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    gap: 30px;
    font-size: 24px;


    background-color: gray;

}
a{
    text-decoration: none;
    color: #fff;
}
a:hover {
    text-decoration: underline;
}
.route {
    display: flex;
    justify-content: center;
    margin-top: 20px;
    font-size: 32px;
}

footer {
    display: flex;
    justify-content: center;
    align-items: center;

    font-size: 16px;
    margin-top: 50px;
}

.active {
    color: rgb(173, 177, 214);
}

.center {
    padding-top: 40px;
    font-size: 18px;
    display: flex;
    justify-content: center;
    gap: 20px;
}